@import url(https://fonts.googleapis.com/css?family=Khula|Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.WordList{text-align:center;list-style:none;padding:0;color:#5D737E;font-family:"Open Sans",sans-serif}.Word>ul{padding:0;padding-bottom:30px}.Definition>li{text-align:center}.dictionary-word{font-weight:bold}.dictionary-pos{font-style:oblique;margin-right:5px;font-size:90%;color:#64B6AC}.Header{margin-top:10px;text-align:center;padding:10px;color:#5D737E;font-family:"Khula",sans-serif}#title{margin-bottom:0;line-height:32px}#subtitle{margin-top:0;font-size:14px;color:#64B6AC}.menu{font-size:24px;position:fixed;top:40px;right:50px;background-color:#FCFFFD}.navigationMenu{justify-content:right}.siteLink{text-decoration:none;color:#64B6AC}.SearchBar{text-align:center;width:100%;margin:auto;max-width:200px;border-radius:2px;border-bottom:1px solid #64B6AC}.SearchBar input{font-family:"Open Sans",sans-serif;border-style:none;background:transparent;outline:none;text-align:center;width:100%;color:#5D737E;font-size:22px}body{background:#FCFFFD}

